.container {
  background-color: #f1f1f1;
  background: url("../imgs/wood_floor.jpg");
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 25px;
  min-height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;

}

/***********************************/
.single-card {
  background-color: #fff;
  border-radius: 5px;
  padding: 4px 10px 10px 2px;
  width: 40px;
  height: 80px;
  float: left;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,.5);
          box-shadow: 0 0 5px rgba(0,0,0,.5);
  z-index: 0;
}

.hand .single-card {
  margin: 0 auto;
  margin-left: -30px;
}

.single-card::after {
  text-align: end;
  width: 12px;
}

.suite-text {
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.value-text {
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.clubs, .spades {
  color: #32475A;
}

.hearts, .diamonds {
  color: #E41800;
}

.hearts::after {
  content: attr(value) ' ♥';
}

.spades::after {
  content: attr(value) ' ♠';
}

.clubs::after {
  content: attr(value) ' ♣';
}

.diamonds::after {
  content: attr(value) ' ♦';
}


.game-area {
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: fixed;
  height: auto;
  width: 300px;
}

.carpet {
  background-image: url('../imgs/carpet.jpg');
  background-size: 300px 400px;
  background-repeat: no-repeat;
  height: 400px;
  width: 300px;
  position: absolute;
}

.carpet-absolute {
  background-size: 300px 400px;
  background-repeat: no-repeat;
  height: 400px;
  width: 300px;
  position: relative;
  padding: 10px;
}

.carpet .single-card {
  border-bottom-width: 10px;
  width:50px;
  height: 60px;
  float: left;
  font-size: 10px;
}

.hand {
  width: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin: 20px 30px;
}

.player {
  position: absolute;
}

.card-back {
  width: 52px;
  height: 94px;
  -webkit-transform: translateY(-100%) rotate(90deg);
      -ms-transform: translateY(-100%) rotate(90deg);
          transform: translateY(-100%) rotate(90deg);
  background: #fff;
  border-radius: 5px;
}

.card-back img {
  width: 100%;
  height: 100%;
}


